@mixin rwd($w) {
  @media only screen and (max-width: $w) {
    @content; } }
@mixin minW($w) {
  @media only screen and (min-width: $w) {
    @content; } }
@mixin rwdh($h:900px) {
  @media only screen and (max-height: $h) {
    @content; } }
@mixin minH($h:900px) {
  @media only screen and (min-height: $h) {
    @content; } }


@mixin clearfix {
  &:after {
    content: '';
    display: block;
    clear: both; } }

@mixin keyframes($name) {
	@-webkit-keyframes #{$name} {
		@content; }
	@-moz-keyframes #{$name} {
		@content; }
	@-ms-keyframes #{$name} {
		@content; }
	@keyframes #{$name} {
		@content; } }
@mixin animation($content) {
	-webkit-animation: $content;
	-moz-animation: $content;
	-o-animation: $content;
	animation: $content; }

@mixin before($txt:'') {
  &:before {
    content: $txt;
    display: block;
    @content; } }

@mixin after($txt:'') {
  &:after {
    content: $txt;
    display: block;
    @content; } }

@mixin hover($w:768px) {
  @media only screen and (min-width: $w) {
    &:hover {
      @content; } } }


@mixin pstc0($t:0,$l:0,$b:0,$r:0) {
  position: absolute;
  top: $t;
  bottom: $b;
  left: $l;
  right: $r;
  margin: auto; }

@mixin pstc5($t:50%,$l:50%) {
  position: absolute;
  top: $t;
  left: $l;
  transform: translate(-50%,-50%); }

@mixin fontH1() {
  font-size: 1.5rem;
  line-height: 1.5; }

@mixin fontH2() {
  font-size: 1.333333333333333rem;
  line-height: 1.5; }

@mixin fontH3() {
  font-size: 1.166666666666667rem;
  line-height: 1.5; }

@mixin fontP() {
  font-size: 1rem;
  line-height: 1.5; }

@mixin dib {
  display: inline-block;
  vertical-align: middle; }

* {
  padding: 0;
  margin: 0px;
  // outline: 1px solid #f00
  font-family: 'Roboto','Noto Sans TC', sans-serif; }

img,iframe {
  width: 100%;
  vertical-align: top; }

html {
  font-size: 24px;
  @include rwd(640px) {
    font-size: 3.75vw; } }

body {
  background-color: #f3c100; }

article {
  max-width: 750px;
  margin: auto;
  box-shadow: 0px 0px 10px #ffcc00;
  background-image: url('../images/bg.png');
  overflow: hidden; }

$red: #cc0000;
$yellow: #ffff00;



